#themes {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 3;
    color: white;
    text-align: center;
    font-family: 'Cormorant SC', serif;

    & span {
        position: relative;
        font-size: 5rem;
        top: 50%;
        transform: translateY(-50%);
    }
}
